import React from "react"
import { graphql } from "gatsby"
import BlogComponent from "../../gatsby-theme-minimal-blog/components/blog"

const BlogPage = ({ data }) => {
  const mergedPosts = data.allPost.nodes.map((post, index) => {
    return {
      ...post,
      frontmatter: data.allMdx.nodes[index].frontmatter
    }
  })
  return (<BlogComponent posts={mergedPosts} />)
}

export default BlogPage

export const query = graphql`
  query($formatString: String!) {
    allPost(sort: { fields: date, order: DESC }) {
      nodes {
        slug
        title
        date(formatString: $formatString)
        excerpt
        timeToRead
        description
        tags {
          name
          slug
        }
      }
    }
    allMdx(sort: { fields: frontmatter___date} ) {
      nodes {
        slug
        frontmatter {
          date
          slug
          author
          email
        }
      }
    }
  }
`
